import React, { useState } from 'react';
import Button from '../button/button.component';
import Input from '../input-tile/input.component';
import Paragraph from '../paragraph/paragraph.component';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';
const Main = () => {
    const [input, setinput] = useState<string>('')
    const onChange = (input: any) => {
        setinput(input)
    }

    return (
        <div className='container mx-auto bg-white shadow-2xl relative rounded-md mt-8 p-4'>
            <div className='flex justify-between mt-2 mb-4'>
                <Button className='text-colortoogel bg-input h-10 w-40' title='فرهنگ سرمایه گذاری' />
                <Button className='text-colortoogel bg-input h-10 w-40' title='راهنمای درگاه' />
            </div>
            <img className='m-auto' src="iamges/logo-with-black-text-vertical.cb73a0fe.svg" alt="logo" />
            <Paragraph textalign='text-center' title='این درگاه به منظور اتصال یکپارچه ذینفعان بازار سرمایه، به خدمات اطلاع‌رسانی و درخواست‌های عملیاتی شرکت سپرده‌گذاری مرکزی اوراق بهادار و تسویه وجوه طراحی شده است' classname='text-black mt-14 text-2xl' />
            <div className='rounded-full bg-worningbackground p-3 m-auto mt-8 w-full md:w-3/5'>
                <p className='text-worning text-base text-center'>توجه : استفاده از این درگاه صرفا برای کاربران<span className='text-worninggreen'> سجامی شده</span> (احراز هویت شده) فراهم گردیده است.</p>
            </div>
            <div className='mt-16 space-y-6'>
                <Input title='کد ملی یا شناسه ملی خود را وارد کنید' placeholder='از صفحه کلید لاتین استفاده فرمایید' />
                <Input value={input} title='رمز عبور را وارد کنید' placeholder='از صفحه کلید لاتین استفاده فرمایید' />
                <p className='text-title text-base text-center'>کاربر گرامی با کلیک بر روی دکمه <span className='text-red-500'>shift</span> امکانات دیگر کیبورد فراهم میگردد</p>
                <div className='m-auto w-full md:w-2/4'>
                    <Keyboard
                        onChange={onChange}
                    />
                </div>
                <div className='flex justify-between w-full md:w-2/4 m-auto'>
                    <img className='rounded-full w-10/12' src="iamges/ksof4yvz724316107.jpg" alt="code" />
                    <button className='bg-refresh mt-3 flex items-center justify-center rounded-full h-14 w-14'>
                        <img src="iamges/refresh.8e9b9a23.svg" alt="refresh" />
                    </button>
                </div>
                <Input placeholder='کد امنیتی را وارد کنید' />
                <div className='m-auto w-full md:w-2/4'>
                    <Button className='text-colortoogel bg-bigbutton h-14 w-full' title='ورود' />
                </div>
                <div className='flex w-full md:w-2/4 m-auto justify-between'>
                    <button><Paragraph title='ثبت نام در سامانه' classname='text-titlebottom text-base' /></button>
                    <button> <Paragraph title='رمز عبور را فراموش کرده ام' classname='text-titlebottom text-base' /></button>
                </div>
                <Paragraph textalign='text-center' title='تماس با پشتیبانی : 1569' classname='text-lasttitle text-base' />
                <div className='border p-3 rounded-lg absolute hidden md:block bottom-5 left-10 w-32'>
                    <img src="iamges/QRnew.ba8c5bfd.svg" alt="qrcode" />
                    <button><Paragraph textalign='text-center' title='دانلود اپلیکیشن' classname='text-black text-base mt-4' /></button>
                </div>
            </div>
        </div>
    );
};

export default Main;