import React from 'react';
import Paragraph from '../paragraph/paragraph.component';

type inputType = {
    title?: string;
    placeholder?: string;
    value?: string;
}
const Input = (props: inputType) => {
    return (
        <div className='m-auto w-full md:w-2/4'>
            {props.title && <Paragraph textalign='text-center' title={props.title} classname='text-black mb-6 text-base' />}
            <input value={props.value} placeholder={props.placeholder} className='rounded-full p-5 bg-input focus:outline-none focus:ring-2 focus:ring-bigbutton focus:border-bigbutton w-full text-center' />
        </div>
    );
};

export default Input;